

.wrap-webmasters .circle-item {
  display: inline-block;
  font-size: 40px;
  color: #fff;
  border: 2px solid #fff;
  border-radius: 30px;
  width: 50px;
  height: 50px;
  /*line-height: 42px;*/
  padding: 0;
  margin-top: 10px;
  cursor: default;
  animation-duration: 1.6s;
  animation-name: pulse;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 5px rgba(255,255,255,0);
  }
  100% {
    box-shadow: 0 0 0 5px rgba(255,255,255,0.5)
  }
}

.slide-container {
  width: 100%;
  height: 100%;
}


.slide-container .wrap-webmasters .web-masters-tilt .initial.active{
  opacity: 1;
}
.wrap{
  height: 580px;
  background-color: black;
  padding-top: 0 !important;
  width: 100%;
  overflow: hidden;
  position: relative;
}

.wrap-webmasters{
  z-index: 20;
  overflow: hidden;
  position: relative;
  -webkit-transition: transform .8s;
  transition: transform .8s;
  width: 85%;
  /*height: 100%;*/
  height: 600px;
  top: 0;
  transform: skewX(-10deg);
  -moz-transform: skewX(-10deg);
  -webkit-transform: skewX(-10deg);
  left: -35%;
  box-shadow: 1px 0 10px rgba(0,0,0,0.7);
  .web-masters{
    width: 100%;
    height: 100%;
    margin-left: 15%;
    background: url(https://www.paypalobjects.com/digitalassets/c/website/marketing/na/us/home/consumer-desktop-hero.jpg) no-repeat center center;
    background-size: cover;
    transform: skewX(10deg);
    -moz-transform: skewX(10deg);
    -webkit-transform: skewX(10deg);
    -webkit-transition: transform .8s;
    transition: transform .8s;
  }

  .web-masters-tilt {
    &:before {
      content: "";
      -webkit-transition: opacity .8s;
      transition: opacity .8s;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(90deg, rgba(0,48,135,0.7), rgba(0,156,222,0.7));
      opacity: 0;
    }
    .initial {
      right: 30%;
      position: absolute;
      width: 400px;
      padding-top: 130px;
      height: auto;
      -webkit-transition: opacity .2s;
      transition: opacity .2s;
      opacity: 0;
      text-align: center;
      h2{
        color: #fff;
      }
      p{
        color: #fff;
        font-size: 22px
      }
    }
  }
}



.wrap-advertisers{
  width: 85%;
  height: 100%;
  position: absolute;
  top: 0;
  right: -25%;
  z-index: 10;
  -webkit-transition: transform .8s;
  transition: transform .8s;
  transform: translateX(-8%);
  -moz-transform: translateX(-8%);
  -webkit-transform: translateX(-8%);
  .advertisers{
    width: 100%;
    height: 100%;
    background: url(https://www.paypalobjects.com/digitalassets/c/website/marketing/na/us/home/merchant-desktop-hero.jpg) no-repeat center center;
    background-size: cover;
    -webkit-transition: transform .8s;
    transition: transform .8s;
  }
}


